import { useState, VFC } from 'react';
import Button from './button';

const Navigation: VFC = () => {
  const [isOpen, setIsOpen] = useState(false);

  const clickGithub = () => {
    window.open('https://github.com/cameronaziz', '_blank');
  };

  const toggleNavigation = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className="container position-sticky z-index-sticky top-0">
      <div className="row">
        <div className="col-12">
          <nav className="navbar navbar-expand-md blur border-radius-md top-0 z-index-fixed shadow position-absolute my-3 py-2 start-0 end-0 mx-4">
            <div className="container-fluid">
              <Button
                label="Contact Me"
              />
              <button
                onClick={toggleNavigation}
                className={`navbar-toggler shadow-none ms-auto ${isOpen ? 'collapsed' : ''}`}
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#navigation"
                aria-controls="navigation"
                aria-expanded={isOpen ? 'true' : 'false'}
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon mt-2">
                  <span className="navbar-toggler-bar bar1"></span>
                  <span className="navbar-toggler-bar bar2"></span>
                  <span className="navbar-toggler-bar bar3"></span>
                </span>
              </button>
              <ul className="navbar-nav navbar-nav-hovers d-inline">
                <li className={`nav-item my-auto ms-3 ms-lg-0 ${isOpen ? 'collapse-show' : 'collapse'} navbar-collapse`}>
                  <Button
                    label="Blog"
                    color="secondary"
                  />
                </li>
              </ul>
              <div className={`${isOpen ? 'collapse-show' : 'collapse'} navbar-collapse py-lg-0`} id="navigation">
                <ul className="navbar-nav navbar-nav-hover ms-lg-12 ps-lg-5 w-100">
                  <li className="nav-item my-auto ms-3 ms-lg-0 ms-lg-auto">
                    <Button
                      color="secondary"
                      icon="github"
                      onClick={clickGithub}
                    />
                  </li>
                </ul>
              </div>
            </div>
          </nav>
        </div>
      </div>
    </div>
  );
};

export default Navigation;
